#footer-container {
	background: $background-color;
	max-width: 100%;
	width: 100%;
	margin: 0 auto;
	border-top: 1px solid #ccc;
	margin-top: rem-calc(60);

	#footer {
		@include grid-row();
		padding: rem-calc(30) 0;

		ul {
			list-style-type: none;
			margin: 0;
			padding: 0;
		}


		.fa{
			color: #fff;
			font-size: 36px;
		}


		.sub-footer{
			margin-top: 15px;
			font-size: 11px;
		}
	}
}
